.slider-container {
  margin: 300px auto;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 700px;
  height: 450px;
}

.current-image {
  border-radius: 0.5rem;
  box-shadow: 0px 0px 7px #555;
  width: 100%;
  height: 100%;
}

.hide-image {
  display: none;
}

.arrow {
  position: absolute;
  width: 2rem;
  height: 2rem;
  color: #fff;
  filter: drop-shadow(0px 0px 5px #555);
}

.arrow-left {
  left: 1rem;
}

.arrow-right {
  right: 1rem;
}

.circle-indicator {
  display: flex;
  position: absolute;
  bottom: 1rem;
}

.current-indicator {
  background-color: #ffffff;
  height: 10px !important;
  width: 10px !important;
  border-radius: 50% !important;
  border: none;
  outline: none;
  margin: 0 0.2rem;
  cursor: pointer;
}

.inactive {
  background-color: grey;
}
